import React, { Component } from 'react'
import './car.css'
import { Card, Button,Input } from 'antd';
import { Select } from 'antd';

const { Option } = Select
export default class Car extends Component {
    state = {
        title: []

    }

    render() {
        let status = ['变更中', '未生效', '作废', '正常', ' 审核中']
        const children = [];


        for (let i = 0; i < status.length; i++) {
            children.push(<Option key={status[i]}>{status[i]}</Option>);
        }

        let handleChange = (value) => {
            //   console.log(`selected ${value}`);
            // let title2 =  this.state.title
            let title2 = []
            title2.push(value + '  ')
            this.setState({ title: title2 })
        }
        return (
            <div>

                <Card className='qqq' title={this.state.title} extra={<a href="#">添加状态</a>}>
                  
                   
                    <Select
                        mode="multiple"
                        allowClear
                        style={{ width: '100%' }}
                        placeholder="Please select"
                        onChange={handleChange}
                    >
                        {/* <Input style={{width:300}}></Input><Button>添加</Button> */}
                        {children}
                    </Select>

                </Card>
            </div>
        )
    }
}
